@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: RobotoCondensed;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: RobotoCondensed;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: PTMono;
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PTMono", "Arial", sans-serif;
  color: #041827;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #f2f5f6;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 10px;
}

.button {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  padding: 10px 15px;
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  border-color: #041827;
  display: block;
}

.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  position: fixed;
  z-index: 100;
  width: 100%;
}
@media (max-width: 1023px) {
  .header {
    position: absolute;
  }
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0 35px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding: 20px 70px 25px 50px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding: 10px 35px 20px 26px;
  }
}

.header__link {
  margin-right: 15px;
}

.header__contacts {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 480px;
}
@media (max-width: 1023px) {
  .header__contacts {
    min-width: 100px;
  }
}
.header__contacts a {
  display: flex;
  align-items: center;
  padding: 10px 0;
}
@media (max-width: 1023px) {
  .header__contacts a {
    padding: 10px;
  }
}
.header__contacts span {
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .header__contacts span {
    display: none;
  }
}

.header__button {
  border-color: #f2f5f6;
  margin-right: 60px;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
    margin-right: 0;
  }
}

@media (max-width: 767px) {
  .header__logo {
    width: 35px;
    height: 35px;
    display: block;
  }
}

.footer {
  background-color: #041827;
  margin-top: auto;
}

.footer__wrapper {
  padding: 50px 0 55px;
  display: flex;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    display: grid;
    grid-template-columns: repeat(2, auto);
    padding: 50px 35px 65px 40px;
    row-gap: 35px;
    column-gap: 130px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    grid-template-columns: 1fr;
    row-gap: unset;
    padding: 0;
    margin: 0 -10px;
  }
}

.footer__socials {
  margin-right: 160px;
}
@media (max-width: 1023px) {
  .footer__socials {
    display: flex;
    justify-content: space-between;
    margin: 0;
    grid-column: 1/-1;
    align-items: center;
  }
}
@media (max-width: 767px) {
  .footer__socials {
    flex-direction: column;
    padding: 35px 55px 25px 50px;
  }
}

.footer__logo-link {
  display: block;
  height: 55px;
}

.footer__social-list {
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: space-between;
  width: 150px;
  margin: 30px 0 0 10px;
}
@media (max-width: 1023px) {
  .footer__social-list {
    margin: 0;
  }
}
.footer__social-list a {
  padding: 5px;
  display: block;
  height: 40px;
}

@media (max-width: 767px) {
  .footer__title-wrapper {
    padding: 20px 25px;
    display: block;
    border-top: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.footer__title {
  font-weight: 400;
  font-size: 15px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f6;
  margin: 0 0 10px 0;
}
@media (max-width: 767px) {
  .footer__title {
    margin: 0;
  }
}

.footer__nav-list {
  display: grid;
  grid-template-rows: repeat(4, auto);
  grid-auto-columns: 220px;
  grid-auto-flow: column;
  column-gap: 30px;
  row-gap: 10px;
  opacity: 70%;
  list-style-type: none;
  padding: 0;
  margin: 20px 30px 0 0;
}
@media (max-width: 1023px) {
  .footer__nav-list {
    grid-template-columns: 1fr;
    grid-auto-columns: unset;
    grid-auto-flow: unset;
    max-width: 220px;
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .footer__nav-list {
    margin: -5px 0 0;
    padding: 0 25px 20px;
    max-width: unset;
  }
}

.footer__contacts {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media (max-width: 767px) {
  .footer__contacts {
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
    gap: unset;
    margin-bottom: 35px;
  }
}

.footer__contacts-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}
@media (max-width: 767px) {
  .footer__contacts-wrapper {
    margin: -5px 0 0;
    padding: 0 25px 20px;
  }
}

.footer__address {
  display: block;
  opacity: 70%;
}

.footer__phone-link {
  display: block;
  opacity: 70%;
}

.accordion {
  position: relative;
}

@media (max-width: 767px) {
  .accordion::before,
.accordion::after {
    content: "";
    position: absolute;
    right: 26px;
    top: 20px;
    width: 1px;
    height: 15px;
    background-color: #f8fdff;
  }
}

.accordion::after {
  transform: rotate(90deg);
}

.accordion.is-open::before {
  transform: rotate(90deg);
}

@media (max-width: 767px) {
  .accordion__content {
    display: none;
  }
}

@media (max-width: 767px) {
  .accordion.is-open .accordion__content {
    display: grid;
  }
}

.main-screen {
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%) no-repeat;
  position: relative;
}
.main-screen picture {
  position: absolute;
  z-index: -10;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}
.main-screen picture img {
  max-width: 100%;
  height: 100%;
}

.main-screen__wrapper {
  width: max-content;
  padding: 305px 0 280px 125px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}
@media (max-width: 1023px) {
  .main-screen__wrapper {
    padding: 180px 0 205px 40px;
    gap: 20px;
  }
}
@media (max-width: 1023px) {
  .main-screen__wrapper {
    padding: 150px 15px 190px 15px;
    margin-right: auto;
    width: 100%;
  }
}

.main-screen__desc {
  font-family: "PTMono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: #f2f5f6;
  max-width: 685px;
}
@media (max-width: 1023px) {
  .main-screen__desc {
    max-width: 670px;
  }
}
@media (max-width: 767px) {
  .main-screen__desc {
    max-width: 271px;
    font-weight: 400;
    font-size: 14px;
  }
}

.main-screen__button {
  display: block;
  font-size: 18px;
  line-height: 21px;
  padding: 15px 25px;
  background-color: #00b2ff;
  width: max-content;
  border: none;
}
@media (max-width: 1023px) {
  .main-screen__button {
    margin-left: 45px;
    margin-top: 30px;
  }
}
@media (max-width: 767px) {
  .main-screen__button {
    font-size: 14px;
    line-height: 16px;
    margin-left: 0;
  }
}

.title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #041827;
  margin: 0;
}
@media (max-width: 767px) {
  .title {
    font-size: 28px;
    line-height: 39px;
  }
}
.title--mini {
  font-size: 24px;
  line-height: 34px;
}
.title--big {
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .title--big {
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .title--big {
    font-size: 36px;
    line-height: 42px;
  }
}

.advantages__wrapper {
  padding: 120px 0 60px;
}
@media (max-width: 1023px) {
  .advantages__wrapper {
    padding: 80px 40px 40px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper {
    padding: 50px 15px 25px;
  }
}

.advantages__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 1fr;
  column-gap: 31px;
  row-gap: 30px;
}
@media (max-width: 1023px) {
  .advantages__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
  }
}

.advantages-item {
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  padding: 40px 45px 30px 40px;
  min-height: 300px;
}
@media (max-width: 1023px) {
  .advantages-item {
    padding: 40px 30px 35px 40px;
    min-height: unset;
  }
}
@media (max-width: 767px) {
  .advantages-item {
    padding: 30px 30px 35px 25px;
    min-height: unset;
    font-size: 14px;
  }
}
.advantages-item svg {
  fill: #00b2ff;
  margin-bottom: 20px;
}
@media (max-width: 1023px) {
  .advantages-item svg {
    margin-bottom: 0;
  }
}
.advantages-item h3 {
  margin-bottom: 15px;
  color: #00b2ff;
}
@media (max-width: 767px) {
  .advantages-item h3 {
    font-size: 20px;
    line-height: 28px;
  }
}
@media (max-width: 767px) {
  .advantages-item span {
    line-height: 19px;
  }
}

.advantages-item:hover {
  background-color: #00b2ff;
  border: 1px solid #00b2ff;
  padding-left: 44px;
  color: #f2f5f6;
}
@media (max-width: 767px) {
  .advantages-item:hover {
    padding-left: 30px;
  }
}
.advantages-item:hover .title {
  color: #f2f5f6;
}
.advantages-item:hover svg {
  fill: #f2f5f6;
}

.about__wrapper {
  padding: 60px 0 100px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .about__wrapper {
    padding: 25px 15px 80px;
  }
}
.about__wrapper picture {
  margin-right: 100px;
}
@media (max-width: 1023px) {
  .about__wrapper picture {
    margin-right: 29px;
  }
}
@media (max-width: 767px) {
  .about__wrapper picture {
    display: none;
  }
}
.about__wrapper picture img {
  max-width: unset;
}

.about__content-wrapper h2 {
  margin-bottom: 15px;
}
.about__content-wrapper p {
  margin: 0;
  overflow: hidden;
}
@media (max-width: 767px) {
  .about__content-wrapper p {
    font-size: 14px;
    line-height: 18px;
  }
}

.about__button {
  margin-top: 20px;
  background-color: transparent;
  border: 1px solid #00b2ff;
  border-radius: 3px;
  color: #00b2ff;
  padding: 14px 13px;
  min-width: 180px;
}

.catalog {
  background-color: #041827;
}

.catalog__wrapper {
  padding: 70px 0 140px;
}
@media (max-width: 1023px) {
  .catalog__wrapper {
    padding: 60px 40px 75px;
  }
}
@media (max-width: 767px) {
  .catalog__wrapper {
    padding: 50px 15px 86px;
  }
}

.catalog__title {
  color: #ffffff;
  margin-bottom: 50px;
}

.catalog__list {
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 85px;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .catalog__list {
    grid-template-columns: 1fr;
    grid-auto-rows: 1fr;
    row-gap: 30px;
  }
}

.catalog__link {
  background-size: cover;
  background-repeat: no-repeat;
  padding: 50px 80px 545px 40px;
  height: 100%;
  display: block;
  width: 100%;
  position: relative;
}
@media (max-width: 1023px) {
  .catalog__link {
    padding: 40px 395px 155px 45px;
  }
}
@media (max-width: 767px) {
  .catalog__link {
    padding: 30px 90px 170px 30px;
  }
}
.catalog__link--production {
  background-image: url("../img/catalog/production-desktop@1x.jpg");
}
