.catalog__link span {
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;
  font-family: "RobotoCondensed", "Arial", sans-serif;
}
@media (max-width: 767px) {
  .catalog__link span {
    font-size: 18px;
  }
}
.catalog__link div {
  position: absolute;
  width: 60px;
  height: 60px;
  background-color: #00b2ff;
  bottom: 0;
  right: 1px;
  border: unset;
  border-radius: 3px 0 0 0;
}

.catalog__link div::before,
.catalog__link div::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 5px;
  background-color: #ffffff;
  transform: rotate(45deg);
  left: 50%;
  top: 30px;
}

.catalog__link div::after {
  transform: rotate(-45deg);
  top: 27px;
}

