.catalog {
  background-color: #041827;
}

.catalog__wrapper {
  padding: 70px 0 140px;

  @include vp-1023 {
    padding: 60px 40px 75px;
  }

  @include vp-767 {
    padding: 50px 15px 86px;
  }
}

.catalog__title {
  color: #ffffff;
  margin-bottom: 50px;
}

.catalog__list {
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 85px;
  list-style-type: none;

  @include vp-1023 {
    grid-template-columns: 1fr;
    grid-auto-rows: 1fr;
    row-gap: 30px;
  }
}

.catalog__link {
  background-size: cover;
  background-repeat: no-repeat;
  padding: 50px 80px 545px 40px;
  height: 100%;
  display: block;
  width: 100%;
  position: relative;

  @include vp-1023 {
    padding: 40px 395px 155px 45px;
  }

  @include vp-767 {
    padding: 30px 90px 170px 30px;
  }

  &--production {
    background-image: url("../img/catalog/production-desktop@1x.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/catalog/production-desktop@2x.jpg");
    }

    @include vp-1023 {
      background-image: url("../img/catalog/production-tablet@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../img/catalog/production-tablet@2x.jpg");
      }
    }

    @include vp-767 {
      background-image: url("../img/catalog/production-mobile@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../img/catalog/production-mobile@2x.jpg");
      }
    }
  }

  &--montage {
    background-image: url("../img/catalog/montage-desktop@1x.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/catalog/montage-desktop@2x.jpg");
    }

    @include vp-1023 {
      background-image: url("../img/catalog/montage-tablet@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../img/catalog/montage-tablet@2x.jpg");
      }
    }

    @include vp-767 {
      background-image: url("../img/catalog/montage-mobile@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../img/catalog/montage-mobile@2x.jpg");
      }
    }
  }

  &--components {
    background-image: url("../img/catalog/components-desktop@1x.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/catalog/components-desktop@2x.jpg");
    }

    @include vp-1023 {
      background-image: url("../img/catalog/components-tablet@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../img/catalog/components-tablet@2x.jpg");
      }
    }

    @include vp-767 {
      background-image: url("../img/catalog/components-mobile@1x.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../img/catalog/components-mobile@2x.jpg");
      }
    }
  }

  span {
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
    font-family: "RobotoCondensed", "Arial", sans-serif;

    @include vp-767 {
      font-size: 18px;
    }
  }

  div {
    position: absolute;
    width: 60px;
    height: 60px;
    background-color: $other-color;
    bottom: 0;
    right: 1px;

    border: unset;
    border-radius: 3px 0 0 0;
  }
}

.catalog__link div::before,
.catalog__link div::after {
  content: "";

  position: absolute;
  width: 1px;
  height: 5px;
  background-color: #ffffff;
  transform: rotate(45deg);
  left: 50%;
  top: 30px;
}

.catalog__link div::after {
  transform: rotate(-45deg);
  top: 27px;
}
